import React from 'react';


function Footer() {
  return (
    <Box bg="teal.600" color="white" py={3} mt={10} boxShadow="md">
      <Flex justify="center">
        <Text fontSize="sm">&copy; {new Date().getFullYear()} Lending Protocol. All rights reserved.</Text>
      </Flex>
    </Box>
  );
}

export default Footer;
