import React from 'react';


function About() {
  return (
    <Box py={8}>
      <Heading mb={4}>About Us</Heading>
      <Text fontSize="lg">
        Lending Protocol is a decentralized finance (DeFi) platform built on Ethereum, enabling users to deposit ETH as collateral, borrow DAI, and participate in automated, secure, and transparent lending and liquidation processes. Powered by Chainlink oracles and automation, our protocol ensures fair pricing, robust security, and innovative features like credit scoring and airdrops.
      </Text>
    </Box>
  );
}

export default About;
