import React from 'react';


function Docs() {
  return (
    <Box py={8}>
      <Heading mb={4}>Documentation</Heading>
      <VStack align="start" spacing={4}>
        <Text>Read our full documentation and whitepaper on GitHub:</Text>
        <Link href="https://github.com/your-repo" color="teal.500" isExternal>
          https://github.com/your-repo
        </Link>
        <Text>For hackathon judges: see the <b>README.md</b> in the root of the repo for setup and architecture details.</Text>
      </VStack>
    </Box>
  );
}

export default Docs;
