import React from 'react';

import { Link as RouterLink } from 'react-router-dom';

function Hero() {
  return (
    <Box textAlign="center" py={12} px={4} bgGradient="linear(to-r, teal.400, blue.400)" color="white" borderRadius="lg" boxShadow="xl" mb={8}>
      <VStack spacing={6}>
        <Image src="https://chain.link/brownie/images/chainlink-logo.svg" alt="Chainlink Logo" boxSize="60px" />
        <Heading as="h1" size="2xl">Lending Protocol</Heading>
        <Text fontSize="xl" maxW="2xl">
          Decentralized, secure, and automated lending on Ethereum. Powered by Chainlink oracles, automation, and randomness for a next-gen DeFi experience.
        </Text>
        <Button as={RouterLink} to="/deposit" colorScheme="teal" size="lg" boxShadow="md">
          Get Started
        </Button>
      </VStack>
    </Box>
  );
}

export default Hero;
