import React from 'react';

import { Link as RouterLink } from 'react-router-dom';

function Navbar() {
  return (
    <Box bg="teal.500" px={4} py={3} color="white" boxShadow="md">
      <Flex align="center">
        <Link as={RouterLink} to="/" fontWeight="bold" fontSize="xl" _hover={{ textDecoration: 'none' }}>
          Lending Protocol
        </Link>
        <Spacer />
        <Flex gap={4}>
          <Link as={RouterLink} to="/" _hover={{ textDecoration: 'underline' }}>Dashboard</Link>
          <Link as={RouterLink} to="/how-it-works" _hover={{ textDecoration: 'underline' }}>How It Works</Link>
          <Link as={RouterLink} to="/deposit" _hover={{ textDecoration: 'underline' }}>Deposit</Link>
          <Link as={RouterLink} to="/borrow" _hover={{ textDecoration: 'underline' }}>Borrow</Link>
          <Link as={RouterLink} to="/repay" _hover={{ textDecoration: 'underline' }}>Repay</Link>
          <Link as={RouterLink} to="/withdraw" _hover={{ textDecoration: 'underline' }}>Withdraw</Link>
          <Link as={RouterLink} to="/airdrop" _hover={{ textDecoration: 'underline' }}>Airdrop</Link>
          <Link as={RouterLink} to="/airdrop-leaderboard" _hover={{ textDecoration: 'underline' }}>Airdrop Winners</Link>
          <Link as={RouterLink} to="/credit-score" _hover={{ textDecoration: 'underline' }}>Credit Score</Link>
          <Link as={RouterLink} to="/liquidation" _hover={{ textDecoration: 'underline' }}>Liquidation</Link>
          <Link as={RouterLink} to="/security" _hover={{ textDecoration: 'underline' }}>Security</Link>
          <Link as={RouterLink} to="/docs" _hover={{ textDecoration: 'underline' }}>Docs</Link>
          <Link as={RouterLink} to="/about" _hover={{ textDecoration: 'underline' }}>About Us</Link>
          <Link as={RouterLink} to="/contact" _hover={{ textDecoration: 'underline' }}>Contact Us</Link>
        </Flex>
      </Flex>
    </Box>
  );
}

export default Navbar;
